import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Grid } from '@material-ui/core';
import SectionHeader from 'components/UI/SectionHeader';

import CustomizedAccordions from './accordian'
import faqcontent from './faqcontent.json'
const useStyles = makeStyles(theme => ({
  root: {},
  image: {
    [theme.breakpoints.down('sm')]: {
      maxWidth: 500,
      marginBottom: 60
    },
  },
  mobileImageContainer: {
    [theme.breakpoints.down('sm')]: {
      position: 'absolute', left: 0, top: -70,
    },
    position: 'absolute', right: 0, top: -70,
  }
}));

const Faq = props => {
  const { setIsSwapDialog, account, className, ...rest } = props;
  const classes = useStyles();
  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });
  return (
    <div id='prologue' className={clsx(classes.root, className)} {...rest}>
      <Grid
        container
        spacing={4}
        direction={isMd ? 'row' : 'column'}
      >
        <Grid
          item
          container
          xs={12}
          md={2}
          data-aos={'fade-up'}
        >
          <SectionHeader
            title={
              <div style={{ display: 'flex', flexDirection: 'column' }}>
                <span style={{ color: theme.palette.text.primary }}>
                  FAQ
                  <br />
                </span>
                <span style={{ color: theme.palette.text.primary, fontSize: 18, fontWeight: '300', textAlign: 'justify', lineHeight: 1.8 }}>

                  <br />
                  <br />
                </span>
              </div>
            }
            align={isMd ? "left" : 'center'}
            disableGutter
            titleVariant="h3"
          />
        </Grid>

        <Grid
          item
          container
          xs={12}
          md={9}
          data-aos={'fade-left'}
        >
          {
            faqcontent.map((content, index) =>{
              return <CustomizedAccordions key = {index} panelid = {index} title = {content.title} content = {content.content} />
            })
          }
          
        </Grid>
      </Grid>
    </div>
  );
};

Faq.propTypes = {
  /**
   * External classes
   */
  className: PropTypes.string,
};


export default Faq;
